html,body,div,span,h1,label,input,select,button{height:inherit;width:inherit;padding:0;margin:0;border:none;outline:none}.soundboard{height:100vh;width:100vw;display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:rgba(69,105,224,0.507)}.synth{display:flex;flex-wrap:wrap;height:468px;width:1700px}.playback-wrapper{display:flex;height:96px;width:672px;box-sizing:border-box;justify-content:space-between;align-items:center}#play,#clear,#random,#algos,#sort{display:flex;align-items:center;justify-content:center;cursor:pointer;height:48px;width:96px;box-sizing:border-box;border-radius:3px;border:1px solid rgba(146,146,146,0.7)}.algo-wrapper{display:flex;height:48px;width:192px}
