.note-wrapper{display:flex;height:36px;width:1700px;padding-top:1px;padding-bottom:1px;box-sizing:border-box}.note{text-indent:4px;display:flex;height:36px;min-width:36px;box-sizing:border-box;font-size:24px;align-items:center}.row{display:flex;height:36px;width:1664px;box-sizing:border-box;align-items:center;justify-content:space-between}.pad-wrapper{position:relative;display:flex;align-items:center;justify-content:center;height:36px;width:52px;padding:1px;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.pad-wrapper:hover input ~ .pad{background-color:#ccc}.pad-wrapper input{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.pad-wrapper input:checked ~ .pad{background-color:#042875af}.pad{display:flex;height:34px;width:50px;box-sizing:border-box;background-color:#eee;border-radius:3px;border:1px solid rgba(146,146,146,0.7)}.highlight{border-right:5px solid #fa2020;width:52px;box-sizing:border-box}.outline{display:flex;background-color:#fa2020;height:34px;width:50px;box-sizing:border-box}
